import Vue from 'vue';
import NavItem from './NavItem.vue';
import NavEventBus from '@/scripts/nav/NavEventBus';
import Sections from '@/scripts/nav/NavItems';
import Events from '@/scripts/nav/Events';
export default Vue.extend({
    name: "NavBar",
    data: function () {
        return {
            sections: Sections
        };
    },
    props: {
        show: {
            type: Boolean,
            required: true
        }
    },
    methods: {
        emitClose: function () { NavEventBus.$emit(Events.closeNav); },
        emitOpen: function () { NavEventBus.$emit(Events.openNav); },
        animFinished: function () {
            console.log('done');
            NavEventBus.$emit(Events.navAnimDone);
        },
    },
    components: {
        "nav-item": NavItem
    }
});



